

























































import { MemberGroupPreview } from '@/apis/models/MemberGroupModel';
import { QuizGroupData, QuizGroupPreview } from '@/apis/models/QuizGroupModel';
import CheckListItem from '@/components/test_maker/CheckListItem.vue';
import Vue from 'vue';

export default Vue.extend({
  components: {
    CheckListItem,
  },
  props: {
    baseGroups: {
      type: Array as () => Array<QuizGroupData | MemberGroupPreview>,
      default: () => ([
      ] as (QuizGroupData | MemberGroupPreview)[]),
    },
    checkedList: {
      type: Array as () => Array<QuizGroupData | MemberGroupPreview>,
      default: () => ([
      ] as (QuizGroupData | MemberGroupPreview)[]),
    },
    groupType: {
      type: String,
      default: '',
    },
    defaultCheckedList: {
      type: Array as () => Array<QuizGroupPreview | MemberGroupPreview>,
      default: () => ([
      ] as QuizGroupPreview[] | MemberGroupPreview[]),
    },
    isLocked: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      searchString: '',
    };
  },
  computed: {
    trimSearchString(): string { // 修掉輸入的空白
      const name = this.searchString.trim();
      if (!name) {
        return name;
      }
      return name;
    },
    searchStringList(): (QuizGroupData | MemberGroupPreview)[] {
      const searchResult = [] as (QuizGroupData | MemberGroupPreview)[];
      this.baseGroups.forEach((groupsObject) => {
        let groupsName = '';
        if (this.groupType === 'member') {
          groupsName = (groupsObject as MemberGroupPreview).uniqueName; // member用的是uniqueName
        }
        if (this.groupType === 'quiz') {
          groupsName = groupsObject.name; // quiz用的是name
        }
        const inputName = this.trimSearchString;
        const indexOf = groupsName.toUpperCase().indexOf(inputName.toUpperCase());
        if (indexOf >= 0) {
          searchResult.push(groupsObject);
        }
      });
      return searchResult;
    },
    filteredGroups(): (QuizGroupData | MemberGroupPreview)[] {
      if (this.trimSearchString !== '') {
        return this.searchStringList;
      }
      return this.baseGroups;
    },
  },
  watch: {
    // updated when api call is finished
    defaultCheckedList() {
      this.initDefaultList();
    },
  },
  methods: {
    initDefaultList() {
      this.defaultCheckedList.forEach((defaultitem: QuizGroupPreview | MemberGroupPreview) => {
        this.baseGroups.forEach((baseitem) => {
          if (defaultitem.uuid === baseitem.uuid) {
            this.checkedList.push(baseitem);

            this.$set(baseitem, 'iconChecked', true);
          }
        });
      });
    },
    onClickListItem(item: QuizGroupData | MemberGroupPreview) {
      // try to find item in check list by uuid
      for (let i = 0; i < this.checkedList.length; i += 1) {
        if (this.checkedList[i].uuid === item.uuid) {
          // found, remove the item from check list and return
          this.checkedList.splice(i, 1);
          return;
        }
      }

      // the item is not exist in the list, add it to the list
      this.checkedList.push(item);
    },
  },
});

