




















import { MemberGroupPreview } from '@/apis/models/MemberGroupModel';
import { QuizGroupData } from '@/apis/models/QuizGroupModel';
import Vue, { PropType } from 'vue';

export default Vue.extend({
  name: 'CheckListItem',
  props: {
    name: {
      type: String,
      required: true,
    },
    count: {
      type: Number,
      required: true,
    },
    item: {
      type: Object as PropType<QuizGroupData | MemberGroupPreview>,
      required: true,
    },
    isLocked: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    handleClick() {
      if (this.isLocked === false) {
        // update checkbox
        if (!this.item.iconChecked) {
          this.$set(this.item, 'iconChecked', true);
        } else {
          this.$set(this.item, 'iconChecked', false);
        }

        // inform parent I am clicked
        this.$emit('toggleSelection');
      }
    },
  },
});

