
















































































import Vue from 'vue';
import QuizEventApi from '@/apis/QuizEventApi';
import { QuizEvent, QuizEventAction } from '@/apis/models/QuizEventModel';
import QuizGroupApi from '@/apis/QuizGroupApi';
import { QuizGroupData } from '@/apis/models/QuizGroupModel';
import MemberGroupApi from '@/apis/MemberGroupApi';
import { MemberGroupPreview } from '@/apis/models/MemberGroupModel';
import checkList from '@/components/test_maker/CheckList.vue';
import checkedItems from '@/components/test_maker/CheckedItems.vue';
import eventSetting from '@/components/test_maker/EventSetting.vue';
import { MessageEventBus } from '@/libs/EventBus';

export default Vue.extend({
  components: {
    checkList,
    checkedItems,
    eventSetting,
  },
  data() {
    return {
      quizEvent: new QuizEvent(), // 本[測驗]的資訊
      quizGroups: [] as QuizGroupData[], // 這是所有的[題組們]
      isLocked: false,
      saved: '',
      monitor: -1,
      nowTimestamp: Math.floor(new Date(Math.floor(Date.now())).getTime() / 1000), // 10位數
      memberGroupsInfo: {
        name: 'member',
        checkedList: [], // 這是已選的[班級]
      },
      quizGroupsInfo: {
        name: 'quiz',
        checkedList: [], // 這是已選的[題組]
      },
      dateTime: {
        startValid: '',
        endValid: '',
        scheduledTime: '',
        defaultdEndTime: '',
        validTimeRemind: '',
        sandTimeRemind: '',
        endTimeRemind: '',
      },
      saveSignalDisplay: false,
      saveSignalTimeoutId: 0,
    };
  },
  computed: {
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
    workspaceId(): string {
      return this.$store.state.currentWorkspace.uuid;
    },
    quizEventId(): string {
      return this.$store.state.currentQuizEvent.uuid;
    },
    memberGroups(): MemberGroupPreview { // 這是所有的[班級們]
      return this.$store.state.theMemberGroups;
    },
  },
  async mounted() {
    this.$store.commit('updateLoading', true);
    MessageEventBus.$on('clear-copied-message', this.clearSaveSignalTimeout);
    this.startStateMonitor();
    try {
      await MemberGroupApi.getMemberGroups(this.workspaceId).then(
        (datalist) => {
          this.$store.commit('getMemberGroups', datalist);
        },
      );
      await QuizGroupApi.getQuizGroups(this.workspaceId).then(
        (datalist) => {
          this.quizGroups = datalist;
        },
      );
      await QuizEventApi.getQuizEvent(this.workspaceId, this.quizEventId).then((quizEvent: QuizEvent) => {
        this.quizEvent = quizEvent;
        this.isLocked = quizEvent.isLocked;
      });
    } finally {
      this.$store.commit('updateLoading', false);
    }
  },
  beforeDestroy() {
    clearInterval(this.monitor);
  },
  methods: {
    startStateMonitor() {
      if (this.monitor !== -1) {
        clearInterval(this.monitor);
      }
      this.monitor = setInterval(async () => {
        QuizEventApi.getQuizEventState(this.workspaceId, this.quizEventId).then((state) => {
          this.isLocked = state.isLocked;
        });
      }, 10 * 1000);
    },
    updateEvent() {
      this.saved = '';
      this.$store.commit('updateLoading', true);
      const startTime = this.dateTime.startValid;
      const endTime = this.dateTime.endValid;
      const scheduled = this.dateTime.scheduledTime;
      if (Date.parse(endTime) > Date.parse(startTime)) {
        this.dateTime.validTimeRemind = '';
        if (this.isLocked === false) { // 沒被鎖
          if (Date.parse(endTime) / 1000 > this.nowTimestamp) {
            this.dateTime.endTimeRemind = '';
            const newinfo = new QuizEvent(this.quizEvent.name, startTime, endTime);
            newinfo.scheduledTime = scheduled;
            newinfo.quizGroups = this.quizGroupsInfo.checkedList;
            newinfo.memberGroups = this.memberGroupsInfo.checkedList;
            if (Date.parse(scheduled) / 1000 > this.nowTimestamp && Date.parse(scheduled) < Date.parse(endTime)) {
              QuizEventApi.update(this.workspaceId, this.quizEventId, newinfo).then(() => {
                this.dateTime.sandTimeRemind = '';
                this.saved = '已儲存';
                this.saveEventSignal();
              }).catch(
                (error) => {
                  console.log('update儲存出錯囉', error);
                },
              );
            } else {
              this.dateTime.sandTimeRemind = '排程發送日期需大於現在，且小於結束日期';
            }
          } else {
            this.dateTime.endTimeRemind = '結束時間需大於現在';
          }
        }
        if (this.isLocked === true) { // 已上鎖
          if (endTime === this.dateTime.defaultdEndTime) {
            this.dateTime.endTimeRemind = '';
            const newinfo = new QuizEvent(this.quizEvent.name, startTime, endTime);
            QuizEventApi.update(this.workspaceId, this.quizEventId, newinfo).then(() => {
              this.saveEventSignal();
            }).catch(
              (error) => {
                console.log('update儲存出錯囉', error);
              },
            );
          } else if (Date.parse(endTime) / 1000 > this.nowTimestamp) {
            this.dateTime.endTimeRemind = '';
            const newinfo = new QuizEvent(this.quizEvent.name, startTime, endTime);
            QuizEventApi.update(this.workspaceId, this.quizEventId, newinfo).then(() => {
              this.saveEventSignal();
            }).catch(
              (error) => {
                console.log('update儲存出錯囉', error);
              },
            );
          } else {
            this.dateTime.endTimeRemind = '結束時間需大於現在';
          }
        }
      } else {
        this.dateTime.validTimeRemind = '結束日期需大於起始日期';
      }
      this.$store.commit('updateLoading', false);
    },
    manualModeUpdate() {
      this.$store.commit('updateLoading', true);
      const newinfo = new QuizEvent(this.quizEvent.name, this.dateTime.startValid, this.dateTime.endValid);
      newinfo.scheduledTime = null;
      newinfo.quizGroups = this.quizGroupsInfo.checkedList;
      newinfo.memberGroups = this.memberGroupsInfo.checkedList;
      if (Date.parse(this.dateTime.endValid) > Date.parse(this.dateTime.startValid)) {
        this.dateTime.validTimeRemind = '';
        QuizEventApi.update(this.workspaceId, this.quizEventId, newinfo).then(() => {
          this.saveEventSignal();
          QuizEventApi.doAction(this.workspaceId, this.quizEventId, QuizEventAction.SEND).then(() => {
            clearInterval(this.monitor);
            this.isLocked = true;
          }).catch(
            (error) => {
              console.log('manualModeUpdate出錯囉', error);
            },
          );
        }).catch(
          (error) => {
            console.log('manualModeUpdate儲存出錯囉', error);
          },
        );
      } else {
        this.dateTime.validTimeRemind = '結束日期需大於起始日期';
      }
      this.$store.commit('updateLoading', false);
    },
    saveEventSignal() {
      MessageEventBus.$emit('clear-copied-message'); // 借用這一個eventbus
      this.saveSignalDisplay = true;
      this.saveSignalTimeoutId = setTimeout(this.clearSaveSignal, 1500);
    },
    clearSaveSignal() {
      this.saveSignalDisplay = false;
    },
    clearSaveSignalTimeout() {
      this.clearSaveSignal();
      clearTimeout(this.saveSignalTimeoutId);
    },
  },
});
